<template>
  <v-sheet
    height="100"
    class="overflow-hidden"
    align="center"
    style="background-color:skyblue;"
  >
    <v-container class="fill-height" style="background-color:blue;margin:1px;padding:1px;">
      <v-row
        align="center"
        justify="center"
        style="background-color:skyblue;"
      >
      <label class="label" @click="goHome"><b>Jeong's Page</b></label>
        <v-col>
          <v-btn v-bind:style="styleObject" dark @click="goTodo">
            Todo
          </v-btn>
        </v-col>
        <v-col>
         <v-btn
          v-bind:style="styleObject" dark @click="goCalcul">
          Calcul
          </v-btn>
        </v-col>
        <v-col>
          <v-btn
          v-bind:style="styleObject" dark @click=showdrawer>
          Title3
          </v-btn>
        </v-col>
        <v-col>
          <v-btn
          v-bind:style="styleObject" dark @click=showdrawer>
          Title4
          </v-btn>
        </v-col>
      </v-row>
    </v-container>
  </v-sheet>
</template>

<script>
export default {
	props : {
    title : String,
    drawer : Boolean
	},
    data () {
      return {
        styleObject: {
          color: 'white'
        }
      }
    },
  methods :{
    goHome() {
      if (this.$route.path !== '/') {
        this.$router.push('/');
      }
    },
    goTodo() {
      if (this.$route.path !== '/todo') {
        this.$router.push('/todo');
      }
    },
    goCalcul() {
      if (this.$route.path !== '/calcul') {
        this.$router.push('/calcul');
      }
    },
    showdrawer() {
      this.$emit('showdrawer')
    }
  }

}
</script>
<style>
  .label {
    font-size: 20px;
    background-color: azure;
    padding: 10px;
    margin: 10px;
  }
</style>